import React, { useState, useEffect, useRef } from "react";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import { ss } from "..";
import { useScroll } from "./CustomHooks";
import JSON_DATA from "../../Data3.json";

import Header from "../../components/Header";
import IntroHolder from "../../components/IntroHolder";
import ContributionChart from "../../components/ContributionChart";
import LinkButton from "../../components/Button";

export default function Viaf2021() {
  // JSON   ContentsId   
  const contentsId = useParams().contentsId;
  const [isScrolled, setScrollActive] = useState(false);
  const [isLoading, setLoading] = useState(false);

  const nav = useNavigate();
  const location = useLocation();

  // const { scrollY } = useScroll();
  var data = null;

  var imagesUrls = [];
  var imagesObjectUrls = [];
  var videoUrls = [];
  const br = useRef();
  const ref01 = useRef();
  const ref02 = useRef();
  var to01 = 0;
  var to02 = 0;

  JSON_DATA.map((item) => {
    if (item.id == contentsId) {
      data = item;
      console.log(data);
      return;
    }
    return <div>Somethings wrong!</div>;
  });

  // +public   
  data.images.map((item) => {
    //  
    imagesUrls.push(process.env.PUBLIC_URL + item);

    //   <-    
    imagesObjectUrls.push({ original: process.env.PUBLIC_URL + item });
    return;
  });

  // +public   
  data.videos.map((item) => {
    videoUrls.push(process.env.PUBLIC_URL + item);
    return;
  });

  useEffect(() => {
    window.scrollTo(0, 0);

    to01 = ref01.current.offsetTop;
    console.log(to01);
    to02 = ref02.current.offsetTop;
    console.log(to02);

    setTimeout(() => {
      setLoading(false);
    }, 500);
  }, []);

  const handleBackEvent = (e) => {
    console.log("Back pressed!");
    // n   history 
  };
  useEffect(() => {
    window.addEventListener("popstate", handleBackEvent);
    return () => window.removeEventListener("popstate", handleBackEvent);
  });

  return (
    <>
      <ss.LoadingPage
        className={isLoading ? "transitionStart" : "transitionEnd"}
      />
      <Header nav={"WORKS"} />
      <ss.HeadContainer textColor="white">
        <ss.HeadContainerImg imageUrl={data.link_banner} />
        <div className="titleText Holder">
          <div className="01">VIA Festival 2021</div>
        </div>
      </ss.HeadContainer>

      {/* <ss.SvgContainer className="SvgCon">
        <ss.SvgDiv
          bott={() => {
            //to01 > x > to02 
            if (window.scrollY > to01 && window.scrollY < to02) {
              console.log("HEWWW");
              return "0%";
            }
            return "50%";
          }}
        >
          <object
            type="image/svg+xml"
            data={process.env.PUBLIC_URL + "/img/svg/aboutCircle.svg"}
          />
        </ss.SvgDiv>
      </ss.SvgContainer> */}

      <ss.Background
        // onLoad={LoadingFunction}
        ref={br}
      >
        <IntroHolder
          title={data.title}
          client={data.client}
          period={data.period}
          subtitle={data.subtitle}
          team={data.team}
          results={data.results.map((item, idx) => {
            return (
              <span key={idx}>
                {item}
                <br />
              </span>
            );
          })}
        />

        <ss.MainContainer className={isScrolled ? "scrolled" : ""}>
          {/* SVG   */}

          <div className="subContainer">
            <ss.TextHolder first={true}>
              <div className="title">
                <div className="bar_start" />
                <div className="text">    </div>
              </div>

              <div className="desc">
                <div>
                  '(VIA)'   (VIA Festival)
                      .  
                       '' 
                   ''      
                  .      
                  ()  .
                </div>
              </div>
              <img
                src={imagesUrls[7]}
                className="holder__image"
                alt="concept"
                style={{ gridColumn: "3 span", width: "100%", height: "auto" }}
              />
              <div className="buttonHolder">
                <LinkButton
                  btnText=""
                  link="https://viaf2021.github.io/viaf2021/"
                />
              </div>
            </ss.TextHolder>
          </div>

          <ss.TechContainer>
            <div className="techHolder__title"> / </div>
            <ss.TechHolder>
              {data.jobs.map((item, idx) => {
                return (
                  <ContributionChart
                    key={"chart" + idx}
                    className="techHolder__chart"
                    jobName={item.job_name}
                    myJob={item.my_job}
                    withTeam={item.with_team}
                    percent={item.percentage}
                  />
                );
              })}
            </ss.TechHolder>
          </ss.TechContainer>

          <div className="subContainer dev" ref={ref01}>
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text"> WebGL </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                    WebGL   .
                  <br />
                        , 
                     .
                  <br />
                        jslib 
                   .
                  <br />
                   2D  3 fbx    
                  .
                </div>
              </div>
            </ss.TextHolder>
          </div>
          <ss.ImageHolder>
            <ss.Holder__image
              src={imagesUrls[6]}
              className="holder__image"
              alt="img01"
              column={"3 / 8 span"}
            />

            <ss.Holder__div column={"4 / 6 span"}>
              <img
                style={{
                  width: "100%",
                  height: "100%",
                  zIndex: "2",
                  position: "relative",
                }}
                src={imagesUrls[9]}
              ></img>
              <video
                style={{
                  position: "absolute",
                  left: "9%",
                  top: "3%",
                  height: "92%",
                }}
                autoPlay
                muted
                loop
                controls={false}
                src={videoUrls[0]}
              ></video>
            </ss.Holder__div>
          </ss.ImageHolder>

          <div className="subContainer des" ref={ref02}>
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">  </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                        
                  .
                  <br />
                       .  
                         
                   .
                  <br />
                         ,
                     .
                </div>
              </div>
            </ss.TextHolder>
          </div>

          <ss.ImageHolder>
            <ss.Holder__image
              src={imagesUrls[2]}
              className="holder__image"
              column={"3 / 8 span"}
              alt="img01"
              objPos="0% 0"
            />
            <ss.Holder__image
              src={imagesUrls[5]}
              className="holder__image"
              column={"3 / 8 span"}
              alt="img02"
            />
            <ss.Holder__image
              src={imagesUrls[4]}
              className="holder__image"
              column={"3 / 4 span"}
              alt="img03"
            />
            <ss.Holder__image
              src={imagesUrls[3]}
              className="holder__image"
              column={"7 / 4 span"}
              alt="img04"
            />
          </ss.ImageHolder>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">  </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                         
                       . 
                     iOS    
                      WebGL    
                  .     Global Illumination
                        .
                </div>
              </div>
            </ss.TextHolder>
          </div>
        </ss.MainContainer>
      </ss.Background>
    </>
  );
}
