import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { ss } from "..";
import JSON_DATA from "../../Data3.json";

import Header from "../../components/Header";
import IntroHolder from "../../components/IntroHolder";
import ContributionChart from "../../components/ContributionChart";
import LinkButton from "../../components/Button";

export default function Doh() {
  // JSON   ContentsId   
  const contentsId = useParams().contentsId;
  const navi = useNavigate();
  const [isScrolled, setScrollActive] = useState(false);
  var data = null;
  var imagesUrls = [];
  var imagesObjectUrls = [];
  var videoUrls = [];

  window.scrollTo(0, 0);

  JSON_DATA.map((item) => {
    if (item.id == contentsId) {
      data = item;
      return;
    }
    return <div>Somethings wrong!</div>;
  });

  // +public   
  data.images.map((item) => {
    //  
    imagesUrls.push(process.env.PUBLIC_URL + item);

    //   <-    
    imagesObjectUrls.push({ original: process.env.PUBLIC_URL + item });
    return;
  });

  // +public   
  data.videos.map((item) => {
    videoUrls.push(process.env.PUBLIC_URL + item);
    return;
  });

  return (
    <>
      <Header nav={"WORKS"} />
      <ss.HeadContainer textColor="white">
        <ss.HeadContainerImg imageUrl={data.link_banner} />
        {/* <ss.BackBtn onClick={() => history.goBack()}></ss.BackBtn> */}
        <div className="titleText Holder">
          <div className="01">Dancing on the Hills</div>
        </div>
      </ss.HeadContainer>

      <ss.Background>
        <IntroHolder
          title={data.title}
          client={data.client}
          period={data.period}
          subtitle={data.subtitle}
          team={data.team}
          results={data.results.map((item, idx) => {
            return (
              <span key={idx}>
                {item}
                <br />
              </span>
            );
          })}
        />

        <ss.MainContainer className={isScrolled ? "scrolled" : ""}>
          {/* SVG   */}
          <ss.SvgImg
            type="image/svg+xml"
            data={process.env.PUBLIC_URL + "/img/svg/aboutCircle.svg"}
            top="14%"
            left="-20%"
          />

          <div className="subContainer">
            <ss.TextHolder first={true}>
              <div className="title">
                <div className="bar_start" />
                <div className="text">    </div>
              </div>

              <div className="desc">
                <div>
                       .
                  <br />
                     . --
                     .
                  <br />
                  --        "Dancing
                  on the hills" .
                  <br />
                      .
                </div>
              </div>
            </ss.TextHolder>
          </div>

          <ss.TechContainer>
            <div className="techHolder__title"> / </div>
            <ss.TechHolder>
              {data.jobs.map((item, idx) => {
                return (
                  <ContributionChart
                    key={"chart" + idx}
                    className="techHolder__chart"
                    jobName={item.job_name}
                    myJob={item.my_job}
                    withTeam={item.with_team}
                    percent={item.percentage}
                  />
                );
              })}
            </ss.TechHolder>
          </ss.TechContainer>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">-- </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                       .
                  <br />
                        .
                  <br />
                         
                   .
                </div>
              </div>
            </ss.TextHolder>
          </div>
          <ss.ImageHolder>
            <ss.Holder__iframe column={"3 / 8 span"}>
              <iframe
                width="100%"
                height="800"
                src="https://www.youtube.com/embed/L2ln5hDMPJM"
                title="DOH"
                rel="0"
                frameBorder="0"
                allow="accelerometer; encrypted-media; gyroscope"
                allowFullScreen
              ></iframe>
            </ss.Holder__iframe>

            <ss.Holder__image
              src={imagesUrls[0]}
              className="holder__image"
              alt="img00"
              column={"3 / 8 span"}
            />
            <ss.Holder__image
              src={imagesUrls[1]}
              className="holder__image"
              alt="img01"
              column={"3 / 8 span"}
            />
            <ss.Holder__image
              src={imagesUrls[6]}
              className="holder__image"
              alt="img02"
              column={"3 / 8 span"}
            />
            <ss.Holder__image
              src={imagesUrls[2]}
              className="holder__image"
              alt="img02"
              column={"3 / 4 span"}
            />
            <ss.Holder__image
              src={imagesUrls[3]}
              className="holder__image"
              alt="img03"
              column={"7 / 4 span"}
            />
            <ss.Holder__image
              src={imagesUrls[7]}
              className="holder__image"
              alt="img00"
              column={"3 / 8 span"}
            />
          </ss.ImageHolder>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">  </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                      .
                  <br />
                        ,  
                   .
                  <br />
                          
                   .
                </div>
              </div>
            </ss.TextHolder>
          </div>
        </ss.MainContainer>
      </ss.Background>
    </>
  );
}
