import React, { useState } from "react";
import { useParams } from "react-router-dom";
import { ss } from "..";
// import JSON_DATA from "../../Data2.json";
import JSON_DATA from "../../Data3.json";

import { Header, IntroHolder, ContributionChart } from "../../components";

export default function Coc2016() {
  // JSON   ContentsId   
  const contentsId = useParams().contentsId;
  const [isScrolled, setScrollActive] = useState(false);
  var data = null;
  var imagesUrls = [];
  var imagesObjectUrls = [];
  var videoUrls = [];

  window.scrollTo(0, 0);

  JSON_DATA.map((item) => {
    if (item.id == contentsId) {
      data = item;
      return;
    }
    return <div>Somethings wrong!</div>;
  });

  // +public   
  data.images.map((item) => {
    //  
    imagesUrls.push(process.env.PUBLIC_URL + item);

    //   <-    
    imagesObjectUrls.push({ original: process.env.PUBLIC_URL + item });
    return;
  });

  // +public   
  data.videos.map((item) => {
    videoUrls.push(process.env.PUBLIC_URL + item);
    return;
  });

  return (
    <>
      <Header nav={"WORKS"} />
      <ss.HeadContainer>
        <ss.HeadContainerImg imageUrl={data.link_banner} />
        {/* <ss.BackBtn onClick={() => history.goBack()}></ss.BackBtn> */}
        <div className="titleText Holder">
          <div className="01">Creative Open Campus</div>
          <div className="02">2016</div>
        </div>
      </ss.HeadContainer>
      <ss.Background>
        <IntroHolder
          title={data.title}
          client={data.client}
          period={data.period}
          subtitle={data.subtitle}
          team={data.team}
          results={data.results.map((item, idx) => {
            return (
              <span key={idx}>
                {item}
                <br />
              </span>
            );
          })}
        />

        <ss.MainContainer className={isScrolled ? "scrolled" : ""}>
          {/* SVG   */}
          <ss.SvgImg
            type="image/svg+xml"
            data={process.env.PUBLIC_URL + "/img/svg/aboutCircle.svg"}
            top="14%"
            left="-20%"
          />

          <div className="subContainer">
            <ss.TextHolder first={true}>
              <div className="title">
                <div className="bar_start" />
                <div className="text">    </div>
                {/* <div className="bar_end" /> */}
              </div>
              <div className="desc">
                <div>
                  +       
                    .
                  <br />
                  Unity3D      
                    ,      
                  .
                  <br />
                         
                     .
                  <br />
                  <br />
                     ,    
                   , UX      ,
                  Unity3D   ,    
                         
                  .
                </div>
              </div>
              {/* COC   */}
              <video muted controls className="items" src={videoUrls[1]} />
            </ss.TextHolder>
          </div>

          <ss.TechContainer>
            <div className="techHolder__title"> / </div>
            <ss.TechHolder>
              {data.jobs.map((item, idx) => {
                return (
                  <ContributionChart
                    key={"chart" + idx}
                    className="techHolder__chart"
                    jobName={item.job_name}
                    myJob={item.my_job}
                    withTeam={item.with_team}
                    percent={item.percentage}
                  />
                );
              })}
            </ss.TechHolder>
          </ss.TechContainer>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">Unity3D    </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                       Unity3D
                  .
                  <br />
                      ,   
                     .
                  <br />
                  <br />
                        
                  .
                  <br />
                    , , ,   
                       
                  .
                </div>
              </div>
            </ss.TextHolder>
          </div>
          <ss.ImageHolder>
            <ss.Holder__image
              src={imagesUrls[10]}
              className="holder__image"
              alt="img02"
              column={"3 / 8 span"}
              objPos="0% 0"
            />
            <ss.Holder__video
              className="holder__video"
              autoPlay
              muted
              loop
              controls={false}
              src={videoUrls[0]}
              column={"2 / 10 span"}
            />
            <ss.Holder__image
              src={imagesUrls[2]}
              className="holder__image"
              alt="img02"
              column={"2 / 5 span"}
              objPos="0% 0"
            />
            <ss.Holder__image
              src={imagesUrls[1]}
              className="holder__image"
              alt="img02"
              column={"7 / 5 span"}
            />
            <ss.Holder__image
              src={imagesUrls[3]}
              className="holder__image"
              alt="img01"
              column={"2 / 10 span"}
              objPos="0% 0"
            />
          </ss.ImageHolder>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">    </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                       ,   
                      .  
                         .
                  <br />
                   ,      
                        
                  .
                </div>
              </div>
            </ss.TextHolder>
          </div>

          <ss.ImageHolder>
            <ss.Holder__image
              src={imagesUrls[6]}
              className="holder__image"
              column={"2 / 5 span"}
              alt="img01"
              objPos="0% 0"
            />
            <ss.Holder__image
              src={imagesUrls[5]}
              className="holder__image"
              column={"7 / 5 span"}
              alt="img02"
              objPos="90% 0"
            />
          </ss.ImageHolder>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">   </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                     /  
                   .
                </div>
              </div>
            </ss.TextHolder>
          </div>

          <ss.ImageHolder>
            <ss.Holder__image
              src={imagesUrls[7]}
              className="holder__image"
              alt="img02"
              column={"2 / span 10"}
            />
            <ss.Holder__image
              src={imagesUrls[8]}
              className="holder__image"
              alt="img02"
              column={"2 / span 5"}
            />
            <ss.Holder__image
              src={imagesUrls[9]}
              className="holder__image"
              alt="img02"
              column={"7 / span 5"}
            />
          </ss.ImageHolder>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">  </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                         
                  .
                  <br />
                        
                         
                    .
                  <br />
                      
                  ()  .  , 
                         
                        .
                </div>
              </div>
            </ss.TextHolder>
          </div>
        </ss.MainContainer>
      </ss.Background>
    </>
  );
}
