import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { ss } from "..";
// import JSON_DATA from "../../Data2.json";
import JSON_DATA from "../../Data3.json";

import Header from "../../components/Header";
import IntroHolder from "../../components/IntroHolder";
import ContributionChart from "../../components/ContributionChart";
import LinkButton from "../../components/Button";

export default function Cardivu() {
  // JSON   ContentsId   
  const contentsId = useParams().contentsId;
  const [isScrolled, setScrollActive] = useState(false);
  var data = null;
  var imagesUrls = [];
  var imagesObjectUrls = [];
  var videoUrls = [];

  window.scrollTo(0, 0);

  JSON_DATA.map((item) => {
    if (item.id == contentsId) {
      data = item;
      return;
    }
    return <div>Somethings wrong!</div>;
  });

  // +public   
  data.images.map((item) => {
    //  
    imagesUrls.push(process.env.PUBLIC_URL + item);

    //   <-    
    imagesObjectUrls.push({ original: process.env.PUBLIC_URL + item });
    return;
  });

  // +public   
  data.videos.map((item) => {
    videoUrls.push(process.env.PUBLIC_URL + item);
    return;
  });

  return (
    <>
      <Header nav={"WORKS"} />
      <ss.HeadContainer textColor="white">
        <ss.HeadContainerImg imageUrl={data.link_banner} />
        {/* <ss.BackBtn onClick={() => history.goBack()}></ss.BackBtn> */}
        <div className="titleText Holder">
          <div className="01">Cardivu</div>
        </div>
      </ss.HeadContainer>

      <ss.Background>
        <IntroHolder
          title={data.title}
          client={data.client}
          period={data.period}
          subtitle={data.subtitle}
          team={data.team}
          results={data.results.map((item, idx) => {
            return (
              <span key={idx}>
                {item}
                <br />
              </span>
            );
          })}
        />

        <ss.MainContainer className={isScrolled ? "scrolled" : ""}>
          {/* SVG   */}
          <ss.SvgImg
            type="image/svg+xml"
            data={process.env.PUBLIC_URL + "/img/svg/aboutCircle.svg"}
            top="23%"
            left="-20%"
          />

          <div className="subContainer">
            <ss.TextHolder first={true}>
              <div className="title">
                <div className="bar_start" />
                <div className="text">    </div>
              </div>

              <div className="desc">
                <div>
                          
                   .
                  <br />
                   iOS   , Android   
                  .
                </div>
              </div>

              {/* <LinkButton link="https://viaf2021.github.io/viaf2021/" /> */}
            </ss.TextHolder>
          </div>

          <ss.TechContainer>
            <div className="techHolder__title"> / </div>
            <ss.TechHolder>
              {data.jobs.map((item, idx) => {
                return (
                  <ContributionChart
                    key={"chart" + idx}
                    className="techHolder__chart"
                    jobName={item.job_name}
                    myJob={item.my_job}
                    withTeam={item.with_team}
                    percent={item.percentage}
                  />
                );
              })}
            </ss.TechHolder>
          </ss.TechContainer>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">  </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                           
                    .
                  <br />
                        
                  .
                </div>
              </div>
            </ss.TextHolder>
          </div>

          <ss.ImageHolder>
            <ss.Holder__image
              src={imagesUrls[0]}
              className="holder__image"
              column={"1 / 12 span"}
              alt="car_img_01"
              objPos="0% 0"
            />
          </ss.ImageHolder>

          <div className="subContainer">
            <ss.TextHolder>
              <div className="title">
                <div className="bar_start" />
                <div className="text">  </div>
                <div className="bar_end" />
              </div>
              <div className="desc">
                <div>
                     Swift    .
                  <br />
                          .
                  <br />
                     JAVA   
                  .
                  <br />
                  Kotlin     .
                </div>
              </div>
            </ss.TextHolder>
          </div>
        </ss.MainContainer>
      </ss.Background>
    </>
  );
}
